	"diagnosis": "由于平时的护理不当导致角质层受损皮脂膜不完整，皮肤内部的水分及营养流失过快，紊乱皮肤的生理功能，导致毛孔堵塞发炎，免疫力降低，因此皮肤出现了脸颊泛红、痘印、粉刺、痤疮等现象",
	"solution": "抗敏——消炎——代谢——修护\n第一阶段：舒缓镇静皮肤，缓解面部潮红敏感现象\n第二阶段：清除炎症反应，防止痤疮交叉感染\n第三阶段：将不属于皮肤内部的垃圾毒素往外代谢，净化皮肤环境，改善吸收\n第四阶段：修护受损细胞，重建角质脂屏障，淡化痘印，恢复皮肤各项机能，使皮肤健康通透有光泽。",
	"hint": "1.补充益生菌促排毒\n2.补充胶原蛋白提高皮肤弹性及修复能力\n3.多喝水促代谢\n4.适当运动促循环",
	"is_public": 1,
	"department_id": 1,
	"editor_id": 387,
	"deleted_at": null,
	"created_at": "2020-12-08 15:01:35",
	"updated_at": "2020-12-08 15:01:35",
	"attentions": "1.注意饮食健康：尽量避免辛辣、刺激性食物，多食疏菜、水果；白天禁食光敏性食物，例如：芹菜、香菜、柠檬、菠菜、油菜、等等。\n2.避免紫外线直接照射，注意防晒\n3.宜用28-32摄氏度的水温洁面，且洗面后用冷水撩面，提高皮肤的耐受力\n4.避免用手去抓、扣、摸、挤痘痘，以免感染杂菌，扩大感染面\n5.注意睡眠，避免熬夜\n6.忌烟酒",
