		"quantity": 1,
		"describe": "蕴含丰富的天然植物萃取滋养成分，清爽柔润细腻，清新嫩肤，滋润紧致，改善粗糙，尤其是膜布中的石墨烯成分，能持久抑菌，吸附肌肤中的污垢与彩妆残留，特别添加烟酰胺成分，击退暗沉，美白亮肤，使肌肤焕发透亮，光滑弹润，散发自然光彩，缔造美丽肌肤。",
